  color: #fff;
  width: 250px;
  margin-bottom: 30px;
}

.section .fifthPage ul .ecommers-item p {
  font-size: 13px;
  color: #fff;
  margin-top: 30px;
  line-height: 2;
}

button {
  background: transparent;
  border: none;
}

.btn-white {
  width: 150px;
  height: 50px;
  font-size: 11px;
  line-height: 50px;
  border: 1px solid #1b1b1c;
  background: #fff;
  text-align: center;
  margin-top: 25px;
  text-transform: uppercase;
  position: relative;
}

.btn-white:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  bottom: 0;
  z-index: -1;
  background: #1b1b1c;
}

.btn-white:hover {
  color: #fff;
  background: transparent;
}

.btn-white:hover:before {
  width: 150px;
}

.btn-black {
  width: 150px;
  height: 50px;
  font-size: 11px;
  line-height: 50px;
  border: 1px solid #1b1b1c;
  background: #1b1b1c;
  text-align: center;
  color: #fff;
  margin-top: 17px;
  text-transform: uppercase;
  cursor: pointer;
}
